import config from '../config.js';

//   Phaser.State https://photonstorm.github.io/phaser-ce/Phaser.State.html
export default class State extends Phaser.State {
    constructor(...args) {
        super(...args);
        // console.log('State.constructor');

        //   this.init   
        this.initOrig = this.init;
        //   this.init   
        this.init = this.initHandler;

        //   this.update   
        // this.updateOrig = this.update;
        //   this.update   
        // this.update = this.updateHandler;

    }

    // updateHandler(...args) {
    //     //   init    
    //     this?.updateOrig?.(...args);
    //     // console.log(1);
    // }

    //   init https://photonstorm.github.io/phaser-ce/Phaser.State.html#init
    initHandler(...args) {
        // console.log('State.init');
        //    scale.   :
        //     ,
        //        

        //  ,    
        this.group = this.add.group();

        //     this.add   
        //   this.world  
        this.add.world = this.group;

        //   init    
        this.initOrig(...args);

        //     scale  .
        //     scale  this.world
        this.game.scale.scaleMode = Phaser.ScaleManager.EXACT_FIT;
        this.game.scale.fullScreenScaleMode = Phaser.ScaleManager.EXACT_FIT;
        this.game.scale.refresh();

        //    this.group  scale,  
        //        , 
        //   
        this.group.scale.set(
            config.scale.x,
            config.scale.y
        );
    }
}
